<template>
  <div class="home">
    <div class="wrap card">
      <img src="@/assets/img/vue-logo.png"/>
      <h2>
        <span  class="underline"> Vue.js </span><br/>
        <span class="underline">Modal Pop-up</span>
      </h2>
      <model ref="model" name="model" title="Заголовок!" :visible.sync="visible">
        <div class="thumb">
         <img src="@/assets/img/thumbnail.svg">
        </div>
      </model>
      <model name="auto-close" title="Автоматическое закрытие окна!" :autoClose="2500">
        <div class="thumb">
         <img src="@/assets/img/thumbnail.svg">
        </div>
      </model>
      <button class="demo-buttom" @click="refs('model')"> Refs </button>
      <button class="demo-buttom" @click="variable(!visible)"> Variable </button>
      <button class="demo-buttom" @click="plugin('auto-close')"> Plugin </button> 
  </div >
  <ul class="circles">
    <li v-for="i in 10" :key="i"></li>
  </ul>
  </div>
</template>

<script>
export default {
  name: 'home',
  data: () => ({
    visible: false
  }),
  methods: {
    variable (visible) {
      this.visible = visible
    },
    refs (name) {
      this.$refs[name].toggle()
    },
    plugin (name) {
      this.$modal.toggle(name)
    }
  }
}
</script>