<template>
   <transition name="modal-fade" @after-enter="afterEnter" @after-leave="afterLeave">
    <div class="modal" 
      :class="[{ 'is-opne': isOpen }]"
      v-show="isOpen" @click.self="close">
        <div  class="modal__wrap">
          <div class="modal__header">
            <slot name="title">
              <span class="modal__title">{{ title }}</span>
            </slot>
            <button class="modal__header-btn" @click="close">
              <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24"><path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z" /></svg>
            </button>
          </div>
          <div class="modal__body">
            <slot></slot>
          </div>
          <div class="modal__footer" v-if="$slots.footer">
            <slot name="footer"/>
          </div>
        </div>
    </div>
 </transition>
</template>
<script>
import {Bus} from './bus'
export default {
  name: 'model',
  props: {
    name: {
      required: true,
      type: String
    },
    title: {
      type: String,
      default: ''
    },
    autoClose: {
      type: Number,
      default: 0
    },
    visible: Boolean
  },
  data: () => ({
    timerId: null,
    isOpen: false,
    methods: ['open', 'close', 'toggle']
  }),
  beforeMount () {
    this.methods.forEach(type => Bus.$on(type + '_' + this.name, this[type]))
  },
  beforeDestroy () {
    this.methods.forEach(type => Bus.$off(type + '_' + this.name, this[type]))
  },
  watch: {
    visible (val) {
      if (val != this.isOpen) {
        return val ? this.open() : this.close()
      }
    }
  },
  methods: {
    open () {
      this.isOpen = true
      this.$emit('open')
      this.$emit('update:visible', true)
      if (this.autoClose) {
        clearTimeout(this.timerId)
        this.timerId = setTimeout(this.close, this.autoClose)
      }
    },
    close () {
      this.isOpen = false
      this.$emit('close')
      this.$emit('update:visible', false)
		},
    toggle () {
      return !this.isOpen ? this.open() : this.close()
    },
    afterEnter () {
      this.$emit('opened')
    },
    afterLeave() {
      this.$emit('closed')
    },
  }
}
</script>