import UIModal from './modal'
import {Bus} from './bus'

export default {
  install (Vue, opts = {}) {
    let plugin = {}
    let {methods} = UIModal.data()
    let key = opts.key || '$modal'
    let componentName = opts.name || UIModal.name
    methods.forEach(type => {
      plugin[type] = (name,params) => Bus.$emit(type + '_' + name, params)
    })
    Vue.prototype[key] = plugin
    Vue.component(componentName, UIModal)
  }
}